.page {
    font-family: 'Inter', 'Helvetica Neue', Arial, sans-serif;
    margin: 0 auto;
    color: white;
    background-color: black;
}

@media (min-width: 320px) {
    .page {
        width: 100%;
    }
}

@media (min-width: 1280px) {
    .page {
        max-width: 1280px;
    }
}